<template>
  <div>
    <img height="100px" src="../assets/senslogo/light.png" alt="">
    <h1>Light {{ this.light }} </h1>
    <div class="mt-3">
      <b-button v-on:click="controlLight(1)" class="mx-4" variant="success">Open Light</b-button>
      <b-button v-on:click="controlLight(0)" class="mx-4" variant="danger">Close Light</b-button>
    </div>
  </div>

</template>

<script>
  // import serviceTAH from '../services/serviceTAH';
  import axios from 'axios';
  const url = 'light';

  export default {
    name: 'Light',
    data() {
      return {
        light: null,
        error: ''
      }
    },
    async created() {
      this.getLight();
      setInterval(this.getLight,2000);
    },
    methods: {
      async getLight() {
        try {
          await axios.get(url).then((response) => {
            // this.TAHdata = response.data;
            this.light = response.data.light;
            // console.log();
          })
        } catch (err) {
          this.error = err.message;
        }
      },
      controlLight: async (comm) =>{
        await axios.post(url,{comm})
      }
    },
  }
</script>

