<template>
  <div>
    <div class="row">
      <div class="col-6">
        <img height="75px" src="../assets/senslogo/temp.png" alt="">
        <h3>Temperature {{ this.temp }} &#8451;</h3>
      </div>
      <div class="col-6">
        <img height="75px" src="../assets/senslogo/hum.png" alt="">
        <h3>Humidity {{ this.hum }}%</h3>
      </div>
    </div>
    <div class="mt-2 row">
      <div class="col-6">
        <b-button v-on:click="controlHeater(1)" class="mx-4 mt-2" variant="success">Open Heater</b-button>
      </div>
      <div class="col-6">
        <b-button v-on:click="controlHeater(0)" class="mx-4 mt-2" variant="danger">Close Heater</b-button>
      </div>
      
    </div>

  </div>

</template>

<script>
  // import serviceTAH from '../services/serviceTAH';
  import axios from 'axios';
  const url = 'TAH';

  export default {
    name: 'TempAndHum',
    data() {
      return {
        TAHdata: null,
        temp: null,
        hum: null,
        error: ''
      }
    },
    async created() {
      this.getTAH();
      setInterval(this.getTAH, 5000);
    },
    methods: {
      async getTAH() {
        try {
          await axios.get(url).then((response) => {
            // this.TAHdata = response.data;
            this.temp = response.data.temperature;
            this.hum = response.data.humidity;
            // console.log();
          })
        } catch (err) {
          this.error = err.message;
        }
      },
      controlHeater: async (comm) => {
        // console.log(comm)
        await axios.post(url,{comm})
      }
    },
  }
</script>