<template>
  <div>
    <img height="100px" src="../assets/senslogo/gas.png" alt="">
    <h1>Gas {{ this.gas }} </h1>
    <div class="mt-3">
      <b-button v-on:click="controlGas(1)" class="mx-4" variant="success">Open Gas</b-button>
      <b-button v-on:click="controlGas(0)" class="mx-4" variant="danger">Close Gas</b-button>
      <!-- <b-button v-on:click="changeMessage()" class="mx-4" variant="warning">Check</b-button> -->
    </div>
  </div>
  

</template>

<script>
  // import serviceTAH from '../services/serviceTAH';
  import axios from 'axios';
  const url = 'gas';

  export default {
    name: 'Gas',
    data() {
      return {
        gas: null,
        error: '',
        newText: 'lolop'
      }
    },
    async created() {
      this.getGas();
      setInterval(this.getGas,1000);
      setInterval(this.changeMessage,1000);
    },
    methods: {
      async getGas() {
        try {
          await axios.get(url).then((response) => {
            // this.TAHdata = response.data;
            this.gas = response.data.gas;
            this.$emit('gasChange',this.gas);
            // console.log();
          })
        } catch (err) {
          this.error = err.message;
        }
      },
      controlGas: async (comm) => {
        // console.log(comm)
        await axios.post(url,{comm})
      },
    },
  }
</script>

